<template lang="pug">
.introContainer
    .introImg(:style="{'background-image': 'url(' + src + ')'}")
        .introMask
            .introText {{text1}}
            h1.TitleU {{name}}
            .introText(v-html="text2")
    
</template>

<script>

export default {
    components: {

    },
    data () {
        return {
            name: "The First Workshop on Multiple Input Modalities and Sensations for VR/AR Interactions (MIMSVAI 2021)",
            src: 'https://mimsvai.github.io/static/imgs/banner.jpg',//image less than 5
            text1: 'September 26, 2021, organised virtually',
            text2: '<span>Workshop in conjunction with </span><a href="https://www.ubicomp.org/ubicomp2021/" target="blank" style="color: #e89700;">UbiComp 2021↗︎</a>'
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
.introContainer
    max-width: 1080px
    margin: auto
.introImg
    max-width: 1280px
    margin: auto
    height: 720px
    border-radius: 8px
    position: relative
    // /*opacity: 0.7; optional will put a slide fade on the background images  */
    background-attachment: fixed
    background-position: right
    background-repeat: no-repeat
    background-color: #FFF
    background-size: cover
.introText
    font-size: 19px
.introMask
    position: inherit
    padding: 20px
    background-color: #ffffffba
    top: 400px
    z-index: 1

@media all and (max-width: 960px)
    .TitleU
        font-size: 25px
    .introMask
        top: 200px

</style>



